  margin: 0 0 24px;
  display: flex;
}

.rating__reviews {
  color: #060b35;
  margin: 0;
}

.stars {
  display: flex;
}

.stars_standard {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+1), .stars__star:nth-child(-n+2), .stars__star:nth-child(-n+3), .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product_price_description {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product_price_description__text {
  margin: 0;
